import React, { useState } from 'react';
import { BrowserRouter as Router, Redirect, Route, Switch } from 'react-router-dom';
import Home from './components/Home';
import Wishlist from './components/Wishlist';
import Cart from './components/Cart';
import Account from './components/Account';
import NavBar from './components/NavBar';
import FilterBar from './components/FilterBar';
import RecentViewedBar from './components/RecentViewedBar';
import Footer from './components/Footer';
import Details from './components/Details';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import useAsync from './components/useAsync';
import PrivateRoute from './components/PrivateRoute';
import { SnackbarProvider } from 'notistack';

function App() {
  const { data, loading } = useAsync('https://online-shop-424e1-default-rtdb.europe-west1.firebasedatabase.app/Products/.json');

  const [keyword, setKeyword] = useState('');
  const [product, setProduct] = useState('');
  const [maxprice, setMaxprice] = useState(16000);
  const [sorting, setSorting] = useState(1);
  const [counter, setCounter] = useState(false);
  const [badge, setBadge] = useState(false);


  const handleSearch = event => {
    setKeyword(event);
  }

  const handleFiltered = (event) => {
    setProduct(event.target.value);
  }

  const handleMaxprice = (event, value) => {
    setMaxprice(value);
  }

  let newData = data.filter(filtered => filtered.id.includes(product))
    .filter(filtered => maxprice === 0 ? filtered.price > 0 : filtered.price <= maxprice)
    .filter(filtered => {
      return (
        filtered.name.replace(/\s/g, '').toLowerCase().match(keyword.replace(/\s/g, '').toLowerCase()) ||
        filtered.specs.some(el => el.replace(/\s/g, '').toLowerCase().match(keyword.replace(/\s/g, '').toLowerCase())) ||
        filtered.description.replace(/\s/g, '').toLowerCase().match(keyword.replace(/\s/g, '').toLowerCase())
      );
    });

  const handleSort = () => {
    if (sorting === 1) {
      setSorting(2);
    } else if (sorting === 2) {
      setSorting(3);
    } else {
      setSorting(1);
    }
  };

  const [viewedProduct] = useState(
    JSON.parse(sessionStorage.getItem("recentViewed")) || []
  )

  const handleViewedProducts = (item) => {
    for (let i = 0; i < viewedProduct.length; i++) {
      if (viewedProduct[i].item.name === item.name) {
        return;
      }
    }
    setCounter(!counter);
    viewedProduct.unshift({ item });
    if (viewedProduct.length === 9) {
      viewedProduct.pop();
    }
    sessionStorage.setItem("recentViewed", JSON.stringify(viewedProduct));
  }


  const handleBadge = () => {
    setBadge(!badge);
  }


  const tonavbar = {
    keyword,
    handleSearch,
    badge
  }


  const tofilterbar = {
    product,
    maxprice,
    handleFiltered,
    handleMaxprice,
    handleSort,
    sorting
  }

  const tohome = {
    product,
    maxprice,
    newData,
    loading,
    sorting,
    keyword,
    handleViewedProducts,
    handleBadge
  }

  const todetails = {
    handleBadge
  }

  const torecentviewed = {
    viewedProduct,
    counter
  }



  return (
    <Router basename={process.env.PUBLIC_URL}>
      <React.Fragment>
        <SnackbarProvider maxSnack={3}>
          <CssBaseline />
          <AppBar>
            <NavBar {...tonavbar} />
            <FilterBar {...tofilterbar} />
          </AppBar>
          <Switch>
            <PrivateRoute path="/wishlist">
              <Wishlist />
            </PrivateRoute>
            <PrivateRoute path="/cart">
              <Cart />
            </PrivateRoute>
            <PrivateRoute path="/account">
              <Account />
            </PrivateRoute>
            <Route path="/details">
              <Details {...todetails} />
            </Route>
            <Route path={["/", "/1"]}>
              <Home {...tohome} />
            </Route>
            <Redirect from="*" to="/" />
          </Switch>
          <RecentViewedBar {...torecentviewed} />
          <Footer />
        </SnackbarProvider>
      </React.Fragment>
    </Router >
  )
}

export default App;
