import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Rating from '@material-ui/lab/Rating';
import Box from '@material-ui/core/Box';
import { Divider } from '@material-ui/core';
import { useLocation } from "react-router-dom";
import useAsync from './useAsync';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import Slideshow from './Slideshow';
import AddToCart from './AddToCart';


const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        padding: 20,
        gap: 20,
        margin: '0 auto',
        maxWidth: 1500,
    },
    aside: {
        minWidth: 500,
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
        '& h3, & h2': {
            margin: '20px 0 5px 0',
        },
        '& p': {
            letterSpacing: 1,
            wordSpacing: 2,
            lineHeight: 1,
            color: '#1c54b2',
            fontWeight: 500,
        },
    },
    rating: {
        margin: '20px 0',
        width: 200,
        display: 'flex',
        alignItems: 'center',
        '& > span': {
            color: '#33c9dc',
            textDecoration: 'underline',
        }
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
    },
    top: {
        color: '#1af0ff',
        animationDuration: '550ms',
        textAlign: 'center',
    },
    circle: {
        strokeLinecap: 'round',
    },
}));

const labels = {
    0.5: 'Useless',
    1: 'Useless+',
    1.5: 'Poor',
    2: 'Poor+',
    2.5: 'Ok',
    3: 'Ok+',
    3.5: 'Good',
    4: 'Good+',
    4.5: 'Excellent',
    5: 'Excellent+',
};



export default function Details(props) {
    const classes = useStyles();

    const handleBadge = props.handleBadge;

    const url = 'https://online-shop-424e1-default-rtdb.europe-west1.firebasedatabase.app/Products/';
    let { data, loading } = useAsync(url + '.json');


    const [value, setValue] = useState();
    const [rated, setRated] = useState();
    const [hover, setHover] = useState(-1);
    const [myRating, setMyRating] = useState();

    const [open] = useState(true);

    const location = useLocation();
    const id = decodeURI(location.pathname.substr(9));
    const indexItem = data.findIndex(x => x.name === id);
    const product = data[indexItem];




    const handleChange = async (newValue) => {
        let ratedTimes = product.ratedTimes + 1;
        let averageRating = ((product.ratedTimes * product.rating + newValue) / ratedTimes);
        await fetch(url + indexItem + '.json', {
            method: 'PATCH',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                "rating": Number(averageRating),
                "ratedTimes": Number(ratedTimes)
            })
        });
        setValue(Math.round(averageRating * 2) / 2);
        setRated(ratedTimes);
        setMyRating(newValue);
    }


    const toaddtocart = {
        handleBadge,
        product
    }

    return (
        <main className={classes.root}>
            {loading && (
                <div>
                    <Backdrop className={classes.backdrop} open={open}>
                        <CircularProgress
                            variant="indeterminate"
                            disableShrink
                            className={classes.top}
                            size={40}
                            thickness={4}
                        />
                    </Backdrop>
                </div>
            )}
            {product &&
                <>
                    <aside className={classes.aside}>
                        <Slideshow product={product} />
                    </aside>
                    <aside className={classes.aside}>
                        <section>
                            <h2>{product.name}</h2>
                            <i>{product.description}</i>
                            <Divider style={{ maxWidth: "100%" }} />
                            <div className={classes.rating}>
                                <Rating
                                    name="hover-feedback"
                                    value={value || product.rating}
                                    precision={0.5}
                                    onChange={(event, newValue) => {
                                        handleChange(newValue);
                                    }}
                                    onChangeActive={(event, newHover) => {
                                        setHover(newHover);
                                    }}
                                />
                                {value !== null && <Box ml={2} mr={2} style={{ fontStyle: 'italic' }}>{labels[hover !== -1 ? hover : Math.round(product.rating * 2) / 2]}</Box>}
                                <span>{rated || product.ratedTimes}</span>
                                <span>&nbsp;ratings</span>
                            </div>
                            {myRating && <h5>You rated {labels[myRating]}</h5>}
                            <h3>Specifications:</h3>
                            <Divider style={{ maxWidth: '100%' }} />
                            {product.specs.map((spec, index) => (
                                <p key={index}>{spec}</p>
                            ))}
                        </section>
                        <AddToCart {...toaddtocart} />
                    </aside>
                </>}
        </main >
    )
}
